  padding: 2px 6px;
  font-size: 9px;
  font-weight: bold;
  position: absolute;
  top: -28px;
  right: -14px;
}

.tooltip:before {
  content: "";
  z-index: -1;
  background-color: #e2e2e2;
  width: 10px;
  height: 10px;
  position: absolute;
  bottom: -2px;
  left: 50%;
